
/* src/components/Header.css */
header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #41292C;
  color: white;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
}

.site-title-box {
  background-color: white;
  padding: 10px 20px;
  border-radius: 5px;
}

.site-title {
  margin: 0;
  font-size: 1.5em;
  color: #41292C;
  font-family: 'Montserrat', sans-serif;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
  flex-grow: 1;
  justify-content: center;
}

.nav-links li a {
  color: white;
  text-decoration: none;
  padding: 10px 15px;
  font-size: 1em;
  transition: background-color 0.3s;
}

.nav-links li a:hover,
.nav-links li a.active {
  background-color: grey;
}

.nav-item-box {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-left: auto;
  flex-shrink: 0;
}

.icon-link {
  color: white;
  padding: 2px 10px;
  font-size: 1.2em;
  transition: color 0.3s;
}

.icon-link:hover {
  color: grey;
}

@media (max-width: 768px) {
  header {
    flex-direction: column;
    align-items: flex-start;
    padding: 10px;
  }

  nav {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
  }

  .nav-links {
    flex-direction: column;
    gap: 10px;
    width: 100%;
    align-items: flex-start;
  }

  .nav-item-box {
    flex-direction: row;
    margin-left: 0;
    margin-top: 10px;
    width: 100%;
    justify-content: flex-start;
  }

  .site-title {
    font-size: 1.2em;
  }

  .nav-links li a {
    font-size: 1em;
    padding: 5px 10px;
  }

  .icon-link {
    font-size: 1em;
  }
}
